import React from 'react';
import { Helmet } from 'react-helmet-async';
import { GoogleMap, useJsApiLoader, Marker } from '@react-google-maps/api';
import locationsData from '../data/locations.json';

// Define the interface for a location
interface Location {
  id: number;
  name: string;
  address: string;
  lat: number;
  lng: number;
}

const containerStyle = {
  width: '100%',
  height: '400px',
  borderRadius: '8px',
};

const center = {
  lat: 20.5937, // A more central latitude for India/surroundings
  lng: 78.9629,
};

const OurOffices: React.FC = () => {
  const { isLoaded } = useJsApiLoader({
    id: 'google-map-script',
    googleMapsApiKey: import.meta.env.VITE_GOOGLE_MAPS_API_KEY || "",
  });

  // Type assertion for the imported JSON data
  const locations: Location[] = locationsData;

  return (
    <>
      <Helmet>
        <title>Our Offices - Neuvantis Technologies</title>
        <meta name="description" content="Find our global office locations. We are strategically positioned to serve our clients across the world." />
      </Helmet>
      <div className="space-y-12">
        <div className="text-center">
          <h2 className="text-4xl font-bold text-primary-800">Our Offices</h2>
          <p className="mt-4 text-lg text-secondary-600 max-w-3xl mx-auto">
            We have a global presence to better serve our clients. Find a Neuvantis office near you.
          </p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-8 items-center">
          <div className="space-y-4">
            {locations.map(location => (
              <div key={location.id} className="p-4 border rounded-lg">
                <h3 className="text-xl font-bold text-primary-700">{location.name}</h3>
                <p className="text-secondary-600">{location.address}</p>
              </div>
            ))}
          </div>
          <div>
            {isLoaded ? (
              <GoogleMap
                mapContainerStyle={containerStyle}
                center={center}
                zoom={4}
              >
                {locations.map(location => (
                  <Marker key={location.id} position={{ lat: location.lat, lng: location.lng }} title={location.name} />
                ))}
              </GoogleMap>
            ) : <div>Loading Map...</div>}
          </div>
        </div>
      </div>
    </>
  );
};

export default OurOffices;
