import React from 'react';
import { Helmet } from 'react-helmet-async';
import leadersData from '../data/leaders.json';
import Leader1 from '../assets/images/leader-1.jpg';
import Leader2 from '../assets/images/leader-2.jpg';

interface Leader {
  id: number;
  name: string;
  title: string;
  bio: string;
  photoUrl: string;
}

const leaderImages = [Leader1, Leader2];

const OurLeaders: React.FC = () => {
  const leaders: Leader[] = leadersData;

  return (
    <>
      <Helmet>
        <title>Our Leaders - Neuvantis Technologies</title>
        <meta name="description" content="Meet the experienced and dedicated leaders driving Neuvantis Technologies forward with their vision and expertise." />
      </Helmet>
      <div className="space-y-12">
        <div className="text-center">
          <h2 className="text-4xl font-bold text-primary-800">Our Leaders</h2>
          <p className="mt-4 text-lg text-secondary-600 max-w-3xl mx-auto">
            Meet the experienced and dedicated team guiding Neuvantis Technologies to new heights of innovation and success.
          </p>
        </div>

        <div className="space-y-16">
          {leaders.map((leader, index) => (
            <div key={leader.id} className={`flex flex-col md:flex-row items-center gap-8 ${index % 2 !== 0 ? 'md:flex-row-reverse' : ''}`}>
              <div className="md:w-1/3 flex-shrink-0">
                <img 
                  src={leaderImages[index]} 
                  alt={leader.name} 
                  className="rounded-lg shadow-lg w-64 h-80 object-cover mx-auto"
                />
              </div>
              <div className="md:w-2/3">
                <h3 className="text-3xl font-bold text-primary-700">{leader.name}</h3>
                <p className="text-xl font-semibold text-primary-600 mb-4">{leader.title}</p>
                <p className="text-secondary-600 text-lg">{leader.bio}</p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </>
  );
};

export default OurLeaders;
