import React, { useState } from 'react';
import { Helmet } from 'react-helmet-async';

interface FormData {
  name: string;
  email: string;
  message: string;
}

const Contact: React.FC = () => {
  const [formData, setFormData] = useState<FormData>({
    name: '',
    email: '',
    message: '',
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    console.log('Form data submitted:', formData);
    alert('Thank you for your message! We will get back to you shortly.');
  };

  return (
    <>
      <Helmet>
        <title>Contact Us - Neuvantis Technologies</title>
        <meta name="description" content="Get in touch with Neuvantis Technologies. We are here to answer your questions about our services and solutions." />
      </Helmet>
      <div className="space-y-12">
        <div className="text-center">
          <h2 className="text-4xl font-bold text-primary-800">Contact Us</h2>
          <p className="mt-4 text-lg text-secondary-600 max-w-3xl mx-auto">
            Have a question or a project in mind? We'd love to hear from you.
          </p>
        </div>

        <div className="bg-white p-8 rounded-lg shadow-lg max-w-2xl mx-auto">
          <form onSubmit={handleSubmit} className="space-y-6">
            <div>
              <label htmlFor="name" className="block text-lg font-medium text-secondary-700">Full Name</label>
              <input type="text" name="name" id="name" required onChange={handleChange} className="mt-1 block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-primary-500 focus:border-primary-500" />
            </div>
            <div>
              <label htmlFor="email" className="block text-lg font-medium text-secondary-700">Email Address</label>
              <input type="email" name="email" id="email" required onChange={handleChange} className="mt-1 block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-primary-500 focus:border-primary-500" />
            </div>
            <div>
              <label htmlFor="message" className="block text-lg font-medium text-secondary-700">Message</label>
              <textarea name="message" id="message" rows={5} required onChange={handleChange} className="mt-1 block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-primary-500 focus:border-primary-500"></textarea>
            </div>
            <div className="text-center">
              <button type="submit" className="bg-primary-600 text-white px-6 py-3 rounded-lg text-lg font-semibold hover:bg-primary-700 transition-colors">Send Message</button>
            </div>
          </form>
        </div>
      </div>
    </>
  );
};

export default Contact;
