import React from 'react';
import { Helmet } from 'react-helmet-async';
import industriesData from '../data/industries.json';

interface Industry {
  id: number;
  name: string;
  description: string;
}

const Industries: React.FC = () => {
  const industries: Industry[] = industriesData;

  return (
    <>
      <Helmet>
        <title>Industries We Serve - Neuvantis Technologies</title>
        <meta name="description" content="Neuvantis provides expert IT solutions for various industries, including Finance, Healthcare, and Retail, helping them innovate and grow." />
      </Helmet>
      <div className="space-y-12">
        <div className="text-center">
          <h2 className="text-4xl font-bold text-primary-800">Industries We Serve</h2>
          <p className="mt-4 text-lg text-secondary-600 max-w-3xl mx-auto">
            Our expertise spans a wide range of industries, allowing us to deliver tailored solutions that address unique challenges and drive success.
          </p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {industries.map((industry) => (
            <div key={industry.id} className="bg-white p-6 rounded-lg shadow-lg hover:shadow-xl transition-shadow duration-300">
              <h3 className="text-2xl font-bold text-primary-700 mb-2">{industry.name}</h3>
              <p className="text-secondary-600">{industry.description}</p>
            </div>
          ))}
        </div>
      </div>
    </>
  );
};

export default Industries;
