import React, { useState } from 'react';
import { Helmet } from 'react-helmet-async';

const jobListings = [
  { id: 1, title: 'Senior Frontend Developer', location: 'Remote' },
  { id: 2, title: 'Cloud Solutions Architect', location: 'Bengaluru, India' },
  { id: 3, title: 'AI/ML Engineer', location: 'Hyderabad, India' },
];

interface FormData {
  name: string;
  email: string;
  resume: File | null;
  coverLetter: string;
}

const Careers: React.FC = () => {
  const [formData, setFormData] = useState<FormData>({
    name: '',
    email: '',
    resume: null,
    coverLetter: '',
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      setFormData({ ...formData, resume: e.target.files[0] });
    }
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    console.log(formData);
    alert('Application submitted successfully!');
  };

  return (
    <>
      <Helmet>
        <title>Careers - Neuvantis Technologies</title>
        <meta name="description" content="Explore career opportunities at Neuvantis Technologies. Join our team of innovators and help us build the future of technology." />
      </Helmet>
      <div className="space-y-16">
        <div className="text-center">
          <h2 className="text-4xl font-bold text-primary-800">Careers at Neuvantis</h2>
          <p className="mt-4 text-lg text-secondary-600 max-w-3xl mx-auto">
            Join our innovative team and help us shape the future of technology. We are looking for passionate individuals to grow with us.
          </p>
        </div>

        <div className="bg-white p-8 rounded-lg shadow-lg">
          <h3 className="text-3xl font-bold text-center text-primary-800 mb-8">Open Positions</h3>
          <div className="space-y-4">
            {jobListings.map(job => (
              <div key={job.id} className="p-4 border rounded-lg flex justify-between items-center">
                <div>
                  <h4 className="text-xl font-bold text-primary-700">{job.title}</h4>
                  <p className="text-secondary-600">{job.location}</p>
                </div>
                <button className="bg-primary-600 text-white px-4 py-2 rounded-lg hover:bg-primary-700 transition-colors">Apply</button>
              </div>
            ))}
          </div>
        </div>

        <div className="bg-white p-8 rounded-lg shadow-lg">
          <h3 className="text-3xl font-bold text-center text-primary-800 mb-8">Apply Now</h3>
          <form onSubmit={handleSubmit} className="space-y-6 max-w-xl mx-auto">
            <div>
              <label htmlFor="name" className="block text-lg font-medium text-secondary-700">Full Name</label>
              <input type="text" name="name" id="name" required onChange={handleChange} className="mt-1 block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-primary-500 focus:border-primary-500" />
            </div>
            <div>
              <label htmlFor="email" className="block text-lg font-medium text-secondary-700">Email Address</label>
              <input type="email" name="email" id="email" required onChange={handleChange} className="mt-1 block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-primary-500 focus:border-primary-500" />
            </div>
            <div>
              <label htmlFor="resume" className="block text-lg font-medium text-secondary-700">Resume</label>
              <input type="file" name="resume" id="resume" required onChange={handleFileChange} className="mt-1 block w-full text-secondary-700 file:mr-4 file:py-2 file:px-4 file:rounded-md file:border-0 file:text-sm file:font-semibold file:bg-primary-50 file:text-primary-700 hover:file:bg-primary-100" />
            </div>
            <div>
              <label htmlFor="coverLetter" className="block text-lg font-medium text-secondary-700">Cover Letter</label>
              <textarea name="coverLetter" id="coverLetter" rows={4} onChange={handleChange} className="mt-1 block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-primary-500 focus:border-primary-500"></textarea>
            </div>
            <div className="text-center">
              <button type="submit" className="bg-primary-600 text-white px-6 py-3 rounded-lg text-lg font-semibold hover:bg-primary-700 transition-colors">Submit Application</button>
            </div>
          </form>
        </div>
      </div>
    </>
  );
};

export default Careers;
